class Shape {
    constructor (options) {
        console.log(options);
        this.canvas = options.canvas;
        this.ctx = options.context;
        this.x = options.x;
        this.y = options.y;
        this.fillStyle = options.fillStyle;
        console.log('shape');
        this.draw();
    }

    buildPath () {
        console.log('shapePath')
    }

    draw (){
        this.ctx.save();
        this.buildPath();
        this.ctx.restore();
    }
}

export default Shape


